import React, { useState } from "react";
import { useContext } from "react";
import Women from "../../../pictures/Women.png";
import { JwModal } from "./JwModal";
import { Link } from "react-router-dom";
import InsideMenu from "./InsideMenu";
import MenuIcon from "@material-ui/icons/Menu";
import { UserContext } from "../../UserContext";
function TopMenu() {
  const [mode, setMode] = useState("");
  const { value, setValue } = useContext(UserContext);
  return (
    <>
      <div className="top-menu">
        <img src={Women} alt="Women's beauty" />

        <span onMouseOver={JwModal.open("custom-modal-2")}>
          <b onMouseOver={() => setMode("makeUp")}>MAKE UP</b>
        </span>

        <span onMouseOver={JwModal.open("custom-modal-2")}>
          <b onMouseOver={() => setMode("hair")}> HAIR</b>
        </span>

        <span onMouseOver={JwModal.open("custom-modal-2")}>
          <b onMouseOver={() => setMode("magazine")}>BEAUTY MAGAZINE</b>
        </span>
        <span onMouseOver={JwModal.open("custom-modal-2")}>
          <b onMouseOver={() => setMode("magazine")}>Other Items</b>
        </span>

        {value && (
          <Link to="/a">
            <div onClick={() => setValue(!value)} className="menu-icon">
              <MenuIcon />
            </div>
          </Link>
        )}
        {!value && (
          <Link to="/">
            <div onClick={() => setValue(!value)} className="menu-icon">
              <MenuIcon />
            </div>
          </Link>
        )}
      </div>
      <JwModal id="custom-modal-2">
        <InsideMenu mode={mode} />
      </JwModal>
    </>
  );
}

export default TopMenu;
